
*{
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: sans-serif;
 
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.heading{
  font-size: 2.5rem;
  font-weight: bold;
  margin: 50px 0;
  color: rgb(255, 247, 250);
}

.bottom-right{
  border-bottom: 4px solid #75f9ff;
  border-right: 4px solid #75f9ff;
}

.bottom{
  border-bottom: 4px solid #75f9ff;
}
.right{
  border-right: 4px solid #75f9ff;
}

.row{
  display: flex;
  flex-direction:row;
  justify-content: center;
  
}

.box{
  width: 20px;
  height: 20px;
  padding: 40px;
  cursor: pointer;
  font-size: 30px;
}

.clearBtn{
  margin: 50px 0;
}
.clearBtn button{
  width: 5rem;
  height: 1.5rem;
  background-color: rgb(255, 247, 250);
  border: none;
  cursor: pointer;
  font-size: 15px;
}

.clearBtn button:hover{
  background-color: #b5f2f7;
}


